import React from 'react';
import ProductDetails from '../components/product_details.js';
import ProductMinimizedListing from '../components/Product_Minimized_Listing.js'


const EnlargeListing = () => {
  //Will be accessed through database
  //sample values right now
  const product = {
    title: "Sample Product",
    seller: "John Doe",
    description: "This is a detailed description of the sample product.",
    datetime_finished: "2025-01-01 12:00",
    status: "Bid Won",
    price: "100",
    timeLeft: "12:23:45",
    yourBid: "70",
    highestBid: "90",
    image_url: "../../public/filler_cat.jpg"

  };

  const minimizedProducts = [
    { id: 1, title: "Product 1", seller: "Seller A", imageUrl: "path/to/image1.jpg" },
    { id: 2, title: "Product 2", seller: "Seller B", imageUrl: "path/to/image2.jpg" },
    { id: 3, title: "Product 3", seller: "Seller C", imageUrl: "path/to/image3.jpg" }
  ];


  return (
    <div className="enlarge-listing">
      <h1>Product Details</h1>
      <ProductDetails 
        title={product.title}
        seller={product.seller}
        description={product.description}
        datetime_finished={product.datetime_finished}
        status={product.status}
        price={product.price}
        timeLeft={product.timeLeft}
        yourBid={product.yourBid}
        highestBid={product.highestBid}
        image_url={product.image_url}
      />
       <h2>Related Products</h2>
      <div className="related_products">
        {minimizedProducts.map(product => (
          <ProductMinimizedListing
            key={product.id}
            title={product.title}
            seller={product.seller}
            imageUrl={product.imageUrl}
          />
        ))}
        </div>
    </div>
  );
}

export default EnlargeListing;
