import React from 'react'
import './Product_mini.css';

const Product_Minimized_Listing = ({image_url, title, seller }) => {
  return (
    <div class = "minimised_listing">
      <div class ="mini_img">
        <img src = {image_url} alt = {title} class = "product-image" />
      </div>

      <div class="info_container">
      <h3 class ="product_title">{title}</h3>
      <p class ="seller_name"><strong>Seller:</strong>{seller}</p>
      </div>  
    </div>
  );
};

export default Product_Minimized_Listing; 