import React from 'react'

const product_details = ({ 
    title, 
    seller, 
    description, 
    datetime_finished, 
    status, 
    price, 
    timeLeft, 
    yourBid, 
    highestBid,
    image_url
  }) =>
  {
  return (
    <div class = "bid-item">
        <div class = "image_url"></div>
        <div className="image-placeholder">
                <img src={image_url} alt={title} className="product-image" />
            </div>
        <div class ="bid-info">
            <h2>{title}</h2>
            <p><strong>{seller}</strong></p>
            <p>{description}</p>
            {datetime_finished && <p><strong>Date time finished:</strong> {datetime_finished}</p>}
            {timeLeft && <p><strong>Time Left:</strong> {timeLeft}</p>}
        </div>
        <div class="bid-status">
            {status === "Auction Cancelled" && <button class = "cancelled">Auction Cancelled</button>}
            {status === "Bid Won" && (
                <>
                    <button class="won">Bid Won</button>
                    <p>Paid: {price}</p>
                    <button>View Receipt</button>
                    </>
            )}
            {status === "Out Bid" && (
                <>
                    <button class="outbid">Out Bid</button>
                    <p>Sold At: {price}</p>
                    {highestBid && <p>Highest Bid: {highestBid}</p>}

                </>
            )}
            {status === "Highest Bidder" && (
                <>
                    <button class="highest_bidder" >Highest Bidder</button>
                    <p>Your Bid: {yourBid}</p>   
                </>
            )}
        </div>
    </div>
  );
};

export default product_details; 